<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <b-table
      ref="selectableTable"
      primary-key="id"
      :items="items"
      :fields="fields"
      striped
      :sort-by="sortBy"
      :sort-desc="sortDesc"
      responsive="sm"
      :busy.sync="loading"
    >
      <template #cell(permissions)="row">
        <b-form-group
          id="input-group-permissions"
          label-class="font-weight-bold"
          label-for="input-user-permissions"
        >
          <b-form-select
            id="input-user-permissions"
            v-model="row.item.permissions"
            :options="permissions"
            :disabled="processing"
            multiple
          >
          </b-form-select>
        </b-form-group>
      </template>

      <template #cell(name)="row">
        {{lookup('roles', row.item.name)}}
      </template>

      <template #cell(details)="row">
        <b-button
          size="sm"
          class="m-1"
          :disabled="processing"
          @click="update(row.item.id, row.item.permissions)"
        >
          Update Permissions <b-spinner v-if="processing" class="mr-3 ml-3" small />
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          :disabled="processing"
          @click="loadPermissions"
        >
          Reset
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          :disabled="processing"
          @click="()=>{row.item.permissions = []}"
        >
          Clear
        </b-button>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import PageHeader from '@/components/PageHeader'
import optionsServices from "@/services/options.services";
import {getPermissions, updatePermissions} from "../../services/auth.services";

export default {
  name: 'permissions-list',
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader
  },
  data () {
    return {
      processing: false,
      loading: false,
      error: false,
      message: '',
      sortBy: 'name',
      sortDesc: true,
      items: [],
      fields: [
        {key: 'name', label: 'Role'},
        {key: 'permissions'},
        {key: 'details', label: ''}
      ]
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    criteria () {
      return {
        role: this.selectRole,
        organization: this.selectOrganization
      }
    },
    roles () {
      return optionsServices.get("roles")
    },
    permissions () {
      return optionsServices.get("permissions")
    }
  },
  methods: {
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    async loadPermissions () {
      const [error, roles] = await getPermissions()
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }

      this.items = (roles || []).map(role => {
        const {
          id=null,
          name='',
          permissions=[]
        } = role || {}

        return {
          id: id,
          name: name,
          permissions: permissions.map(permission => {return permission.name}),
        }
      })
    },
    async update(id, permissions) {
      this.processing = true
      const [error, ] = await updatePermissions(id, permissions)
      if (error) {
        this.$store.commit('setMessage', {
            text: 'Permissions could not be saved. An error occurred.',
            type: 'danger'
          }
        )
      }
      else {
        this.$store.commit('setMessage', {
            text: 'Permissions updated successfully!',
            type: 'success'
          }
        )
      }
      // reload data
      await this.loadPermissions()
      this.processing = false
    }
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    await this.loadPermissions()
    this.$store.commit('setLoading', false)
  }
}
</script>
