<template>
  <b-container fluid v-if="!isLoading && isAdmin" class="p-2 rounded bg-secondary text-white">

    <b-modal
      id="modal-edit-role"
      ref="modal-edit-role"
      :title="lookup(type, selectAction).toString()"
      scrollable
      @ok="updateRole()"
    >
      <template #modal-footer="{ ok, cancel }">
        <b-button :disabled="!selectOption" size="sm" variant="info" @click="ok()">
          {{lookup(type, selectAction).toString()}}
        </b-button>
        <b-button size="sm" variant="secondary" @click="cancel()">
          Cancel
        </b-button>
      </template>
      <div>
        <b-container class="p-2 rounded">
          <b-row>
            <b-col>
              <b-form-select
                id="table-select-action"
                v-model="selectOption"
                :options="options"
                :state="!!selectOption"
              ></b-form-select>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </b-modal>

    <b-row>
      <b-col cols="4">
        <b-form-select
          id="table-select-action"
          v-model="selectAction"
          :options="actions"
        ></b-form-select>
      </b-col>
      <b-col>
        <b-button
          class="m-1 border-light font-weight-bold"
          size="sm"
          variant="info"
          :disabled="processing || !hasAction || !hasSelected"
          @click="applyAction"
        >
          Apply Action
        </b-button>
      </b-col>
      <b-col cols="6" align="right">
        <b-spinner v-if="processing" class="mr-3 ml-3" small />
        <span v-if="selectedItems.length > 0"> {{ selectedItems.length }} Item(s) Selected</span>
        <b-button
          class="m-1 border-light font-weight-bold"
          size="sm"
          variant="dark"
          :disabled="processing"
          @click="selectAll">
          Select All
        </b-button>
        <b-button
          class="m-1 border-light font-weight-bold"
          size="sm"
          variant="dark"
          :disabled="processing || !hasSelected"
          @click="clearSelected">
          Clear Selected
        </b-button>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>

import optionsServices from '@/services/options.services'
import {updateUserRole} from "../services/auth.services";

export default {
  name: "modal-action",
  props: ['loader', 'selectAll', 'clearSelected'],
  data() {
    return {
      type: 'userActions',
      processing: false,
      selectAction: '',
      actions: optionsServices.get('userActions'),
      selectedActionProcess: null,
      selectOption: '',
      options: optionsServices.get("roles")
    }
  },
  computed: {
    selectedItems() {
      return this.$store.getters.getSelected || []
    },
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    hasAction() {
      return !!this.selectAction
    },
    hasSelected() {
      return this.selectedItems.length > 0
    },
  },
  methods : {
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    async applyAction() {
      console.log(this.selectedItems)
      if (this.validateAction()) {
        if (this.selectAction === 'role') {
          this.$bvModal.show('modal-edit-role')
        }
      }
    },
    async updateRole() {
      // buld up
      this.processing = true
      let hasError = true
      await Promise.all(this.selectedItems.map(
        async (user) => {
          const { id=null } = user || {}
          const [error,] = await updateUserRole(id, {role: this.selectOption})
        if (error) hasError = true
      }))

      // check if update failed
      if (!hasError) this.$store.commit('setMessage', {
        text: 'An error occurred. Your action could not be completed',
        type: 'danger'
      })
      else {
        // successful action
        this.$store.commit('setMessage', {
          text: `User(s) action ${this.selectAction.toUpperCase()} was successful!`, type: 'success'
        })
      }
      // reset selections
      this.selectOption = ''
      this.selectAction = ''
      this.$store.commit('setSelected', [])
      this.clearSelected()
      // reload data
      await this.loader()
      this.processing = false
    },
    validateAction() {
      // confirm registration is confirmed and no ceremony opt-out
      const currUser = this.$store.getters.getUser
      const {email=null} = currUser || {}
      const sameUser = this.selectedItems
        .filter(user => {return user.email && (user.email === email)}).length > 0

      // user is attempting to update themselves
      if (sameUser) {
        this.$store.commit('setMessage', {
          text: 'Cannot apply action to your own user account.',
          type: 'danger'
        })
        return false
      }
      return true;
    },
  }
};
</script>
